.slider-container {
    position: relative;
    width: 100%;
    margin-bottom: 55px;
}

.slider-label {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 16px;
    color: #dcdcdc;
    margin-bottom: 8px;
    position: absolute;
    top: -32px;
    left: 0;
}

.slider-track {
    position: relative;
    width: 100%;
    height: 20px;
    background: #323232;
    border-radius: 0;
}

/* REMOVE the fill bar */
.slider-fill {
    display: none;
}

.slider-knob {
    position: absolute;
    width: 10px;
    height: 100%;
    background: #00ff88;
    border-radius: 0;
    transform: translateX(-50%);
    filter: drop-shadow(0 0 12px rgba(61, 255, 129, 0.484));
    transition: left 0.1s ease-out;

}

.slider-value {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 16px;
    color: #c8c8c8;
    text-align: center;
    margin-top: 2px;
    position: absolute;
    top: 100%;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
}