/* App.css */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Disable text selection */
* {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.guide-content *,
.modal-body * {
  -webkit-user-select: text;
  -moz-user-select: text;
  -ms-user-select: text;
  user-select: text;
}

:root {
  --dark-bg: #101010;
  --darker-bg: #0d0d0d;
  --accent-green: #00ff88;
  --accent-green-dark: #00cc6a;
  --accent-green-darker: #00994d;
  --text-primary: #e0e0e0;
  --text-secondary: #a0a0a0;
  --glass-bg: rgba(21, 21, 21, 0.6);
  --glass-border: rgba(23, 23, 23, 0.1);
}

body {
  font-family: 'Segoe UI', system-ui, sans-serif;
  background: var(--dark-bg);
  color: var(--text-primary);
  overflow-x: hidden;
  line-height: 1.6;
}

.app {
  min-height: 100vh;
}

/* Navigation */
.navbar {
  position: fixed;
  top: 0;
  width: 100%;
  padding: 1rem 0;
  z-index: 1000;
  background: transparent;
  transition: all 0.3s ease;
  backdrop-filter: blur(0px);
}

.navbar.scrolled {
  background: var(--glass-bg);
  backdrop-filter: blur(20px);
  padding: 0.75rem 0;
}

.nav-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  font-size: 1.5rem;
  font-weight: bold;
  color: var(--accent-green);
}

.nav-links {
  display: flex;
  list-style: none;
  gap: 2rem;
}

.nav-icon {
  font-size: 1rem;
  vertical-align: middle;
  transform: translateY(-1px);
  /* move slightly up for perfect alignment */

  /* subtle nudge for perfect optical alignment */
}

.nav-link {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  /* space between icon and text */
  background: none;
  border: none;
  color: var(--text-primary);
  cursor: pointer;
  padding: 0.5rem 1rem;
  border-radius: 8px;
  transition: all 0.3s ease;
  font-size: 0.9rem;
  position: relative;
  overflow: hidden;
}

.nav-link::before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 2px;
  background: var(--accent-green);
  transition: width 0.3s ease;
}

.nav-link:hover::before {
  width: 100%;
}

.nav-link:hover {
  color: var(--accent-green);
}

/* Hero Section */
/* Hero Section */
.hero-section {
  min-height: 100vh;
  /* Reduced from 150vh to 120vh */
  display: flex;
  align-items: flex-start;
  padding-top: 8rem;
  position: relative;
  background: linear-gradient(135deg, var(--darker-bg) 0%, var(--darker-bg) 100%);
  overflow: hidden;
  z-index: 2;
  /* Add this */
}

.hero-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background:
    radial-gradient(circle at 10% 20%, rgba(0, 255, 136, 0.03) 0%, transparent 40%),
    radial-gradient(circle at 90% 80%, rgba(0, 255, 136, 0.02) 0%, transparent 35%),
    radial-gradient(circle at 30% 70%, rgba(0, 255, 136, 0.015) 0%, transparent 30%),
    radial-gradient(circle at 70% 30%, rgba(0, 255, 136, 0.01) 0%, transparent 25%);
  pointer-events: none;
}

.hero-container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 2rem;
  display: grid;
  grid-template-columns: 1.2fr 0.8fr;
  /* Changed from 1fr 1fr to give more space to left column */
  gap: 4rem;
  align-items: start;
  width: 100%;
  padding-top: 7rem;
}

@media (max-width: 768px) {
  .hero-container {
    padding-top: 8em;

  }
}

@media (max-width: 414px) {
  .hero-container {
    padding-top: 2em;
  }
}

.hero-content {
  z-index: 2;
  margin-left: 5rem;
  /* Add this to push content to the right */
  /* OR */
  /* padding-left: 2rem; */
  /* Alternative approach */
}

@media (max-width: 768px) {
  .hero-content {
    margin-left: 3rem;
  }
}

/* Mobile devices */
@media (max-width: 636px) {
  .hero-content {
    margin-left: 1rem;
  }
}


.hero-subtitle {
  font-size: 3.5rem;
  font-weight: 700;
  line-height: 1.1;
  margin-bottom: 1.5rem;
  background: linear-gradient(135deg, #fff 0%, var(--accent-green) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.gradient-text {
  background: linear-gradient(135deg, var(--accent-green) 0%, #00ccff 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.hero-description {
  font-size: 1.2rem;
  color: var(--text-secondary);
  margin-bottom: 2.5rem;
  max-width: 500px;
}

.hero-buttons {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
  align-items: center;
}

.download-btn,
.features-btn,
.github-btn {
  padding: 1rem 2rem;
  border: none;
  border-radius: 12px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

/* Icon alignment fixes */
.download-btn svg,
.features-btn svg,
.github-btn svg {
  font-size: 1.1em;
  /* Fine-tune vertical alignment */
}

.download-btn {
  background: linear-gradient(135deg, var(--accent-green) 0%, var(--accent-green-dark) 100%);
  color: var(--dark-bg);
}

.features-btn {
  background: linear-gradient(135deg, #ff6b35 0%, #f7931e 100%);
  color: white;
}

.github-btn {
  background: linear-gradient(135deg, #251c34 0%, #302146 100%);
  color: white;
}

.features-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 30px rgba(255, 107, 53, 0.4);
  background: linear-gradient(135deg, #ff7b45 0%, #f8a33e 100%);
}

.download-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 30px rgba(0, 255, 136, 0.3);
}

.github-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 30px rgba(110, 84, 148, 0.4);
  background: linear-gradient(135deg, #46335e 0%, #302244 100%);
}

/* Shifter Container */
.shifter-container {
  z-index: 1;
  /* Changed from 10 to 1 */
  display: flex;
  justify-content: center;
  align-items: center;
}

.shifter-3d {
  transform: perspective(1000px) rotateX(5deg) rotateY(-15deg) scale(1.2);
  transform-style: preserve-3d;
  transition: all 0.3s ease;
}


/* Scroll Indicator */
.scroll-indicator {
  position: absolute;
  bottom: 2rem;
  left: 50%;
  transform: translateX(-50%);
}

@media (max-width: 768px) {
  .scroll-indicator {
    display: none;
  }
}

.scroll-arrow {
  width: 2px;
  height: 30px;
  background: var(--accent-green);
  position: relative;
  animation: bounce 2s infinite;
}

.scroll-arrow::before {
  content: '';
  position: absolute;
  bottom: -8px;
  left: 50%;
  transform: translateX(-50%);
  width: 10px;
  height: 10px;
  border-right: 2px solid var(--accent-green);
  border-bottom: 2px solid var(--accent-green);
  transform: translateX(-50%) rotate(45deg);
}

@keyframes bounce {

  0%,
  20%,
  50%,
  80%,
  100% {
    transform: translateY(0);
  }

  40% {
    transform: translateY(-10px);
  }

  60% {
    transform: translateY(-5px);
  }
}

/* Second Section */
.second-section {
  display: none;
}

.hero-settings {
  margin-top: 3rem;
  max-width: 500px;
}

.hero-settings .settings-glass {
  background: var(--glass-bg);
  backdrop-filter: blur(20px);
  border: 1px solid var(--glass-border);
  border-radius: 20px;
  padding: 1rem;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.1);
  transform: perspective(1000px) rotateY(15deg) rotateX(5deg);
  transform-style: preserve-3d;
  transition: transform 0.3s ease;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 500px;
  /* Control width here instead */
  margin: 0 auto;
  /* Center the entire glass panel */
}

.second-container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 2rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  align-items: start;
}

.settings-wrapper {
  position: sticky;
  top: 120px;
}


/* Customization Heading */
.customization-heading {
  margin: 20em 0 2rem 0;
  text-align: center;
}

.customization-title {
  font-size: 1.8rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
  background: linear-gradient(135deg, var(--accent-green) 0%, #00ccff 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.customization-description {
  color: var(--text-secondary);
  font-size: 1.1rem;
  max-width: 400px;
  margin: 0 auto;
}

.shifter-placeholder {
  height: 500px;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Features Section */
.features-section {
  padding: 6rem 0;
  background: var(--dark-bg);
  position: relative;
  /* Add this */
  z-index: 5;
  /* Add this to ensure it's above the shifter */
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
}

.section-title {
  font-size: 3rem;
  text-align: left;
  margin-bottom: 4rem;
  background: linear-gradient(135deg, var(--accent-green) 0%, #fff 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.features-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 2rem;
}

.feature-card {
  background: var(--glass-bg);
  backdrop-filter: blur(20px);
  border: 1px solid var(--glass-border);
  border-radius: 16px;
  padding: 2rem;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.feature-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(90deg, transparent, var(--accent-green), transparent);
}

.feature-card:hover {
  transform: translateY(-5px);
  border-color: var(--accent-green);
  box-shadow: 0 20px 40px rgba(0, 255, 136, 0.1);
}

.feature-icon {
  font-size: 2.5rem;
  margin-bottom: 1rem;
}

.feature-card h3 {
  font-size: 1.3rem;
  margin-bottom: 1rem;
  color: var(--accent-green);
}

.feature-card p {
  color: var(--text-secondary);
  line-height: 1.6;
}

/* Download Section */
.download-section {
  padding: 6rem 0;
  background: linear-gradient(135deg, var(--dark-bg) 0%, var(--darker-bg) 100%);
}

.download-card {
  background: var(--glass-bg);
  backdrop-filter: blur(20px);
  border: 1px solid var(--glass-border);
  border-radius: 24px;
  padding: 4rem;
  text-align: center;
  max-width: 800px;
  margin: 0 auto;
  position: relative;
  overflow: hidden;
}

.download-card::before {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: radial-gradient(circle, rgba(0, 255, 136, 0.1) 0%, transparent 70%);
  animation: rotate 20s linear infinite;
}

@keyframes rotate {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.download-card h2 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
  position: relative;
  z-index: 2;
}

.download-card p {
  font-size: 1.2rem;
  color: var(--text-secondary);
  margin-bottom: 2.5rem;
  position: relative;
  z-index: 2;
}

.download-buttons {
  display: flex;
  gap: 1rem;
  justify-content: center;
  flex-wrap: wrap;
  margin-bottom: 2rem;
  position: relative;
  z-index: 2;
}

.primary-download,
.secondary-download {
  padding: 1.2rem 2.5rem;
  border: none;
  border-radius: 12px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.primary-download {
  background: linear-gradient(135deg, var(--accent-green) 0%, var(--accent-green-dark) 100%);
  color: var(--dark-bg);
}

.secondary-download {
  background: transparent;
  color: var(--accent-green);
  border: 2px solid var(--accent-green);
}

.primary-download:hover {
  transform: translateY(-2px);
  box-shadow: 0 15px 35px rgba(0, 255, 136, 0.4);
}

.secondary-download:hover {
  background: rgba(0, 255, 136, 0.1);
  transform: translateY(-2px);
}

.support-links {
  display: flex;
  justify-content: center;
  gap: 2rem;
  flex-wrap: wrap;
  position: relative;
  z-index: 2;
}

.support-link {
  color: var(--text-secondary);
  text-decoration: none;
  transition: color 0.3s ease;
}

.support-link:hover {
  color: var(--accent-green);
}

/* Footer */
.footer {
  background: var(--darker-bg);
  padding: 2rem 0;
  text-align: center;
  border-top: 1px solid var(--glass-border);
}

/* Responsive Design */
@media (max-width: 1200px) {

  .hero-container,
  .second-container {
    grid-template-columns: 1fr;
    gap: 2rem;
  }

  .hero-subtitle {
    font-size: 2.5rem;
  }

  .shifter-container {
    position: relative !important;
    transform: none !important;
    top: auto !important;
    right: auto !important;
  }

  .shifter-3d {
    transform: perspective(800px) rotateX(5deg) rotateY(-5deg);
  }
}

@media (max-width: 768px) {
  .nav-links {
    display: none;
  }

  .hero-subtitle {
    font-size: 2rem;
  }

  .features-grid {
    grid-template-columns: 1fr;
  }

  .download-buttons {
    flex-direction: column;
    align-items: center;
  }

  .download-card {
    padding: 2rem;
  }
}

/* Requirements Section */
.requirements-section {
  padding: 6rem 0;
  background: var(--dark-bg);
}

.requirements-content {
  max-width: 800px;
  margin: 0 auto;
}

.requirement-item {
  display: flex;
  align-items: flex-start;
  gap: 1.5rem;
  margin-bottom: 3rem;
  padding: 2rem;
  background: var(--glass-bg);
  border: 1px solid var(--glass-border);
  border-radius: 16px;
}

.req-icon {
  font-size: 2rem;
  flex-shrink: 0;
}

.req-text h3 {
  color: var(--accent-green);
  margin-bottom: 0.5rem;
  font-size: 1.3rem;
}

.req-text p {
  color: var(--text-secondary);
  margin-bottom: 1rem;
}

.req-link {
  color: var(--accent-green);
  text-decoration: none;
  font-weight: 600;
  transition: all 0.3s ease;
}

.req-link:hover {
  color: var(--accent-green-dark);
}

/* Guides Section */
.guides-section {
  padding: 6rem 0;
  background: var(--dark-bg);
}

.guides-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
}

.guide-card {
  background: var(--glass-bg);
  backdrop-filter: blur(20px);
  border: 1px solid var(--glass-border);
  border-radius: 16px;
  padding: 0.2rem;
  transition: all 0.3s ease;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  /* Make sure this is present */
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  min-height: 200px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.guide-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  transition: all 0.3s ease;
  z-index: 1;
}

.guide-background::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(16, 16, 16, 0.854);
}

.guide-content {
  position: relative;
  z-index: 2;
}

.guide-card:hover {
  transform: translateY(-5px);
  border-color: var(--accent-green);
  box-shadow: 0 20px 40px rgba(0, 255, 136, 0.2);
}

.guide-card:hover::before {
  background: linear-gradient(135deg, rgba(16, 16, 16, 0.8) 0%, rgba(13, 13, 13, 0.7) 100%);
}

.guide-header {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 1rem;
  position: relative;
  z-index: 2;
}

.guide-icon {
  font-size: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

.guide-card h3 {
  font-size: 1.2rem;
  margin: 0;
  color: var(--accent-green);
  line-height: 1.3;
  position: relative;
  z-index: 2;
}

.guide-preview {
  color: var(--text-secondary);
  margin-bottom: 0;
  line-height: 1.6;
  font-style: italic;
  position: relative;
  z-index: 2;
}

.feature-tag {
  background: rgba(0, 255, 136, 0.1);
  color: var(--accent-green);
  padding: 0.3rem 0.8rem;
  border-radius: 20px;
  font-size: 0.8rem;
  border: 1px solid rgba(0, 255, 136, 0.2);
}

/* FAQ Section */
.faq-section {
  padding: 6rem 0;
  background: var(--dark-bg);
}

.faq-grid {
  max-width: 800px;
  margin: 0 auto;
}

.faq-card {
  background: var(--glass-bg);
  backdrop-filter: blur(20px);
  border: 1px solid var(--glass-border);
  border-radius: 16px;
  padding: 2rem;
  margin-bottom: 1.5rem;
  transition: all 0.3s ease;
}

.faq-card:hover {
  border-color: var(--accent-green);
  box-shadow: 0 10px 30px rgba(0, 255, 136, 0.1);
}

.faq-card h3 {
  color: var(--accent-green);
  margin-bottom: 1rem;
  font-size: 1.2rem;
}

.faq-card p {
  color: var(--text-secondary);
  line-height: 1.6;
}

/* Responsive Design for new sections */
@media (max-width: 768px) {
  .requirement-item {
    flex-direction: column;
    text-align: center;
  }

  .guides-grid {
    grid-template-columns: 1fr;
  }

  .guide-features {
    justify-content: center;
  }
}

