.youtube-embed {
  position: relative;
  width: 100%;
  height: 0;
  padding-bottom: 56.25%;
  /* 16:9 aspect ratio */
  margin: 2rem 0;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
}

.youtube-embed iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: none;
}

.guide-content {
  padding: 2rem;
  overflow-y: auto;
  flex: 1;
}

/* ReactMarkdown content styling */
.guide-content h1,
.guide-content h2,
.guide-content h3,
.guide-content h4 {
  color: var(--accent-green);
  margin: 1.5rem 0 1rem 0;
  line-height: 1.3;
}

.guide-content h1 {
  font-size: 1.8rem;
  border-bottom: 1px solid var(--glass-border);
  padding-bottom: 0.5rem;
}

.guide-content h2 {
  font-size: 1.5rem;
}

.guide-content h3 {
  font-size: 1.3rem;
}

.guide-content h4 {
  font-size: 1.1rem;
}

.guide-content p {
  color: var(--text-secondary);
  margin-bottom: 1rem;
  line-height: 1.6;
}

.guide-content strong {
  color: var(--accent-green);
  font-weight: 600;
}

.guide-content em {
  color: var(--text-secondary);
  font-style: italic;
}

.guide-content ul,
.guide-content ol {
  color: var(--text-secondary);
  margin: 1rem 0;
  padding-left: 2rem;
}

.guide-content li {
  margin: 0.5rem 0;
  line-height: 1.5;
}

.guide-content blockquote {
  border-left: 3px solid var(--accent-green);
  padding-left: 1rem;
  margin: 1.5rem 0;
  color: var(--text-secondary);
  font-style: italic;
  background: rgba(0, 255, 136, 0.05);
  padding: 1rem;
  border-radius: 0 8px 8px 0;
}

.guide-content hr {
  border: none;
  height: 1px;
  background: linear-gradient(90deg, transparent, var(--accent-green), transparent);
  margin: 2rem 0;
}

/* Image styling */
.guide-content img {
  max-width: 100%;
  height: auto;
  border-radius: 12px;
  margin: 1.5rem 0;
  border: 1px solid var(--glass-border);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.3);
  display: block;
}

.guide-content img:hover {
  border-color: var(--accent-green);
  transform: translateY(-2px);
  transition: all 0.3s ease;
}

/* Code and inline code */
.guide-content code {
  background: rgba(0, 255, 136, 0.1);
  color: var(--accent-green);
  padding: 0.2rem 0.4rem;
  border-radius: 4px;
  font-family: 'Courier New', monospace;
  font-size: 0.9em;
}

.guide-content pre {
  background: var(--dark-bg);
  border: 1px solid var(--glass-border);
  border-radius: 8px;
  padding: 1rem;
  margin: 1.5rem 0;
  overflow-x: auto;
}

.guide-content pre code {
  background: none;
  color: var(--text-primary);
  padding: 0;
}

/* Links */
.guide-content a {
  color: var(--accent-green);
  text-decoration: none;
  transition: color 0.3s ease;
}

.guide-content a:hover {
  color: var(--accent-green-dark);
  text-decoration: underline;
}

/* Scrollbar for guide content */
.guide-content::-webkit-scrollbar {
  width: 6px;
}

.guide-content::-webkit-scrollbar-track {
  background: var(--dark-bg);
}

.guide-content::-webkit-scrollbar-thumb {
  background: var(--accent-green);
  border-radius: 3px;
}

.guide-content::-webkit-scrollbar-thumb:hover {
  background: var(--accent-green-dark);
}

.guide-preview {
  color: var(--text-secondary);
  font-style: italic;
  margin-top: 1rem;
  text-align: center;
}

.guide-card {
  cursor: pointer;
}

.guide-card:hover {
  transform: translateY(-8px);
}

/* Update existing guide styles */
.guide-header {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.guide-header h3 {
