/* Image Loading Styles for Guide Modal - CSS Only Approach */
.guide-content .image-container {
  position: relative;
  display: block;
  width: 100%;
  border-radius: 12px;
  overflow: hidden;
  margin: 1.5rem 0;
  background: var(--darker-bg);
  min-height: 200px;
}

.guide-content img {
  width: 100%;
  height: auto;
  border-radius: 12px;
  border: 1px solid var(--glass-border);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.3);
  transition: all 0.3s ease;
  display: block;
  position: relative;
  z-index: 2;
  opacity: 1;
}

/* Gradient overlay that shows on all images initially */
.guide-content img::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg,
      transparent,
      rgba(0, 255, 136, 0.3),
      transparent);
  animation: gradientFlow 2s infinite;
  border-radius: 12px;
  pointer-events: none;
  z-index: 1;
}

/* Hide the gradient overlay when image is loaded */
.guide-content img.loaded::before {
  display: none;
}

.guide-content img:hover {
  border-color: var(--accent-green);
  transform: translateY(-2px);
}

@keyframes gradientFlow {
  0% {
    left: -100%;
  }

  100% {
    left: 100%;
  }
}

/* YouTube embed loading state */
.youtube-embed {
  position: relative;
  width: 100%;
  height: 0;
  padding-bottom: 56.25%;
  margin: 2rem 0;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
  background: var(--darker-bg);
}

.youtube-embed::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg,
      transparent,
      rgba(0, 255, 136, 0.2),
      transparent);
  animation: gradientFlow 3s infinite;
  z-index: 1;
}

.youtube-embed iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: none;
  z-index: 2;
}

/* Gradient overlay - shown by default */
.guide-background::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg,
      transparent,
      rgba(0, 255, 136, 0.3),
      transparent);
  animation: gradientFlow 2s infinite;
  z-index: 2;
  pointer-events: none;
  opacity: 1;
  transition: opacity 0.5s ease;
}

/* Hide gradient when background is loaded */
.guide-background.loaded::before {
  opacity: 0;
  animation: none;
}

@keyframes gradientFlow {
  0% {
    left: -100%;
  }

  100% {
    left: 100%;
  }
}