.rails-svg {
    position: absolute;
    top: 0;
    left: 0;
    pointer-events: none;
}

.rail-line {
    stroke: #787878;
    stroke-width: 6;
    stroke-dasharray: none;
}

.neutral-line {
    stroke: #787878;
    stroke-width: 6;
}

.gear-position {
    position: absolute;
    border-radius: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    pointer-events: none;
    z-index: 5;
    overflow: hidden;
    border: 2px solid;
    filter: drop-shadow(0 0 12px rgba(180, 180, 180, 0.146));

}


.gear-position.active {
    background: #c8c8c8;
    border-color: #969696;
    box-shadow: none;
    transform: scale(1);
}

.gear-label {
    font-weight: bold;
    color: #969696;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    line-height: 0.9;
    /* Increased from 0.8 for better spacing */
    width: 100%;
    text-align: center;
}

.gear-position.active .gear-label {
    color: #1e1e1e;
    font-weight: bold;
}

.knob {
    /* Remove fixed width/height from CSS */
    position: absolute;
    border-radius: 50%;
    z-index: 10;
    display: flex;
    align-items: center;
    justify-content: center;
    pointer-events: none;
}

.knob.engaged {
    background: #b4b4b4;
    border-color: #969696;
    /* Add glow effect */

}

.knob.snapped {
    background: #b4b4b4;
    border-color: #969696;
    /* Add glow effect */

    filter: drop-shadow(0 0 12px rgba(180, 180, 180, 0.776));
}




/* Responsive design */
@media (max-width: 600px) {


    .app h1 {
        font-size: 2rem;
    }

    .shifter-container {
        width: 100%;
        max-width: 500px;
        height: 300px;
    }

    .info {
        width: 100%;
        max-width: 500px;
    }

    body {
        cursor: auto;
    }
}

